import React, { Component } from 'react'; 
import { withRouter } from 'react-router-dom';


//bootstrap
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import CarbonxPages from '../../main/carbonx_pages';

//lang & styles
import './register.css';

const thisPage = CarbonxPages.filter(el => el.key==="reg")[0];

class RegisterPage extends Component {
    // notes:

    render(){
    
        //console.log (thisPage);

        return (
            <>
                <Row><Col>{thisPage.icon}&nbsp; {thisPage.title}</Col></Row>
                <Row><Col>
                    <p><br />use this to explain the 3 different types of users:
                        <ol>
                            <li>Environmental Companies who sell their carbon credits</li>
                            <li>Industry Companies that buy credits with for their Offsets AND can offer redeemable Offsets</li>
                            <li>Consumers who can buy credits and redeem them for merch</li>
                        </ol>
                    </p>
                </Col></Row>

            </>
        );
    }
}


export default withRouter(RegisterPage);
//export default BuyPage;